.cards {
  padding: 4rem;
  background: url("../images/mt-hood.jpg") center center/cover no-repeat;
}

.cards > h2 {
  text-align: center;
  margin-top: -50px;
  /* padding-top: 80px; */
  color: #220901;
  opacity: 0.7;
  border: 2px solid;
}

.cards > h3 {
  text-align: center;
  margin-top: -50px;
  font-size: 50px;
  font-weight: bold;
  color: #220901;
}

.cards > p {
  color: #220901;
  text-align: center;
  font-size: 32px;
  font-weight: bold;
  margin-top: 10px;
}

.cards__container {
  display: flex;
  opacity: 0.72;
  flex-flow: column;
  align-items: center;
  max-width: 1120px;
  width: 90%;
  margin: 0 auto;
}

.cards__wrapper {
  position: relative;
  margin: 50px 0 45px;
}

.cards__items {
  margin-bottom: 24px;
}

.cards__item {
  display: flex;
  flex: 1;
  margin: 0 1rem;
  border-radius: 10px;
  background: #fff;
}

.cards__item__link {
  display: flex;
  flex-flow: column;
  width: 100%;
  box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
  -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
}

.cards__item__pic-wrap {
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}

.fade-img {
  animation-name: fade-img;
  animation-duration: 2s;
}

/* label */
.cards__item__pic-wrap::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin-left: 10px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 12px;
  font-weight: 700;
  color: #fff;
  background-color: #BA2D0B;
  box-sizing: border-box;
}

.cards__item__img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
  background: rgb(219, 218, 218);
}

.cards__item__img:hover {
  transform: scale(1.2);
  opacity: 1.0;
}

.cards__item__info {
  padding: 20px 30px 30px;
  background: #fff;
}

.cards__item__text {
  color: #242424;
  font-size: 18px;
  line-height: 24px;
  text-align: center;
}

@media only screen and (min-width: 1200px) {
  .content__blog__container {
    width: 84%;
  }
}

@media only screen and (min-width: 1024px) {
  .cards__items {
    display: flex;
  }
}

@media only screen and (max-width: 1024px) {
  .cards__item {
    margin-bottom: 2rem;
  }
}