    .welcome-container {
    background: url('../../assets/images/proverb.jpg') center center/cover no-repeat;
    height: 100vh;
    width: 100%;
    opacity: 0.7;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
  }
  
  .welcome-container > h1, h2 {
    color: #220901;
    font-size: 100px;
    margin-top: -100px;
  }

  .welcome-container > small {
    color: #220901;
    font-size: 32px;
    font-weight: bold;
  }
  
  .welcome-container > p {
    margin-top: 8px;
    color: #242424;
    font-weight: bold;
    font-size: 32px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
      'Lucida Sans', Arial, sans-serif;
  }
  
  .welcome-btns {
    margin-top: 32px;
  }
  
  .welcome-btns .btn {
    margin: 6px;
  }
  
  .fa-play-circle {
    margin-left: 4px;
  }
  
  @media screen and (max-width: 960px) {
    .welcome-container > h1 {
      font-size: 70px;
      margin-top: -150px;
    }
  }
  
  @media screen and (max-width: 768px) {
    .welcome-container > h1 {
      font-size: 50px;
      margin-top: -100px;
    }
  
    .welcome-container > p {
      font-size: 30px;
    }

  
    .btn {
      width: 100%;
    }
  }